ul{list-style-type:disc;margin-left:2rem}td,th{border-width:1px;padding:.25rem .75rem}table{display:block;overflow-x:auto}tr:nth-child(2n){--tw-bg-opacity:1;background-color:rgb(249 250 251/var(--tw-bg-opacity))}p{padding-bottom:.5rem;padding-top:.5rem}code{--tw-bg-opacity:1;background-color:rgb(243 244 246/var(--tw-bg-opacity));padding:.25rem}h3>code,h4>code{font-weight:600;padding:.25rem}h3>code{font-size:1.25rem;line-height:1.75rem}pre>code{display:block;overflow-x:auto;padding:1rem}h1,h2{border-bottom-width:1px;padding-bottom:.75rem}